  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.activity__cover img {
  width: 100%;
}
.activity__content {
  margin-left: 20px;
}
.activity__title {
  display: block;
